// Function to get status color
export const getStatusColor = (status: string): string => {
  switch (status) {
    case 'VULNERABLE':
      return 'bg-error';
    case 'POTENTIAL':
      return 'bg-yellow-500';
    case 'NOT VULNERABLE':
      return 'bg-success';
    default:
      return 'bg-gray-500';
  }
};

